// constants
import { UPDATE_PREVIEW_IMAGE } from "store/constants";

// utils
import { createReducer } from "../_utils";

const url =
  "data:image/png;base64,";

const initialState = {
  data: url
};

const previewImageHandlers = {
  [UPDATE_PREVIEW_IMAGE.REQUEST]: state => {
    return { ...state, isLoaded: false };
  },
  [UPDATE_PREVIEW_IMAGE.SUCCESS]: (state, action) => {
    const { payload: { data = url } = {} } = action;
    return { ...state, data, isLoaded: true };
  },
  [UPDATE_PREVIEW_IMAGE.FAILURE]: state => {
    return { ...state, error: true };
  }
};

export default createReducer(initialState, previewImageHandlers);
