import React from "react";
import { Link } from "react-router-dom";

// styles
import "./logo.css";

const defaultImage =
  "data:image/png;base64,";

export default ({
  logo = defaultImage,
  to = "/",
  className = "",
  alt = "avatar"
}) => {
  return (
    <Link to={to} className={`avatar__box ${className}`}>
      <img src={logo} alt={alt} />
    </Link>
  );
};
