// utils
import { createReducer } from "../_utils";

// constants
import { FETCH_IMAGE } from "store/constants";

const url =
  "data:image/png;base64,";

const initialState = {
  isLoaded: false,
  error: false,
  data: url
};

const avatarHandlers = {
  [FETCH_IMAGE.REQUEST]: state => {
    return { ...initialState, ...state, isLoaded: false };
  },
  [FETCH_IMAGE.SUCCESS]: (state, action) => {
    const { payload: { data } = {} } = action;
    return {
      ...initialState,
      ...state,
      isLoaded: true,
      data: data.length ? data : url
    };
  },
  [FETCH_IMAGE.FAILURE]: state => {
    return { ...initialState, ...state, error: true };
  }
};

export default createReducer(initialState, avatarHandlers);
