// constants
import {
  UPDATE_SELECTED_COMPANY,
  REMOVE_SELECTED_COMPANY
} from "store/constants";

// utils - reducers
import { createReducer } from "./_utils";

const initialState = {
  avatar:
    "data:image/png;base64,"
};

const selectedCompanyHandlers = {
  [UPDATE_SELECTED_COMPANY]: (state, action) => {
    const { payload = {} } = action;
    return { ...initialState, ...state, ...payload };
  },
  [REMOVE_SELECTED_COMPANY]: () => {
    return { ...initialState };
  }
};

export default createReducer(initialState, selectedCompanyHandlers);
